












d3 = require 'd3'

module.exports =
  name: 'vline',
  data: () ->
    msg: 'Hi!'
    data: [1, 10, 23, 30, 33, 20]
    line: ''
  mounted: () ->
      @calculatePath()
  methods:
    getScales: () ->

          x = d3.scaleTime().range([0, 430])
          y = d3.scaleLinear().range([210, 0])
          d3.axisLeft().scale(x)
          d3.axisBottom().scale(y)

          x.domain(d3.extent(@data, (d, i) => i))
          y.domain([0, d3.max(@data, (d) => d)])

          { x, y }

    calculatePath: () ->
      scale = this.getScales()
      path = d3.line()
        .x((d, i) => scale.x(i))
        .y((d) => scale.y(d))

      @line = path(this.data)




